import React from "react";
import PropTypes from "prop-types";
import red from "@material-ui/core/colors/red";

import {withStyles} from "@material-ui/core";
import {deepOrange, lightBlue, orange} from "@material-ui/core/colors";

const kwStyle = theme => ({
        kw: {
            color: lightBlue[700],
            fontWeight: 500,
        },
        vup: {
            color: lightBlue[700],
            fontWeight: 500,
        },
        rem: {
            color: orange['A400'],
            fontWeight: 500,
        },
        vdowm: {
            color: deepOrange[500],
            fontWeight: 500,
        },
        talpu:{
            color: orange['A400'],
            fontWeight: 500,
        },
        warn: {
            color: red[700],
            fontWeight: 500,
        },
    }
);

function desParserRaw(props) {
    const {classes, jsxStuff, text} = props;
    switch (jsxStuff) {
        case '':
            return (
                <span>{text}</span>
            );
        case 'br':
            return (
                <br/>
            );
        case '@ba.kw':
            return (
                <span className={classes.kw}>{text}</span>
            );
        case '@ba.vup':
            return (
                <span className={classes.vup}>{text}</span>
            );
        case '@ba.vdown':
            return (
                <span className={classes.vdowm}>{text}</span>
            );
        case '@ba.rem':
            return (
                <span className={classes.rem}>{text}</span>
            );
        case '@ba.talpu':
            return (
                <span className={classes.talpu}>{text}</span>
            );
        case '@ba.warn':
            return (
                <span className={classes.warn}>{text}</span>
            );
        case '@cc.vup':
            return (
                <span className={classes.vup}>{text}</span>
            );
        case '@vv.kw':
            return (
                <span className={classes.kw}>{text}</span>
            );
        case '@cc.vdown':
            return (
                <span className={classes.vdowm}>{text}</span>
            );
        default:
            console.log('Undefined tag:' + jsxStuff);
            return <span className={classes.kw}>{text}</span>
    }
}
desParserRaw.propTypes = {
    classes: PropTypes.object.isRequired,
    jsxStuff: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired,
};
const InnerDesParser = withStyles(kwStyle)(desParserRaw);

function desDataDispatch(raw,blackboard) {
    const illegal = /[\[\]\.]/g;
    // let re_percent=/:([0\.]*)%/;
    let re_digit=/\.([0]+)/;
    let rawCopy = raw;
    for (let data of blackboard) {
        let reKey = data.key.replace(illegal,"\\$&");
        let re = new RegExp('\\{[+-]?' + reKey + '(:[0\.%]*)?\\}', 'ig');
        let m = re.exec(raw);
        while (re.lastIndex > 0) {
            let isMinus = m[0][1]==='-';
            let isDecimal = m[0].match(re_digit);
            let isPercent = m[0].includes('%');
            let value = (isPercent?100:1)*(isMinus ? -1 : 1) * data.value;
            if(m[0].includes(':'))
                rawCopy=rawCopy.replace(m[0], value.toFixed(isDecimal?isDecimal[1].length:0)+(isPercent?'%':''));
            else
                rawCopy=rawCopy.replace(m[0],value.toString());
            m = re.exec(raw);
        }
    }
    return rawCopy
}

function DescriptionParser(props) {
    const {raw, blackboard}=props;
    let des = raw === null ? 'None' : raw;
    des = des.replace('\\n', '<br></>');
    if(blackboard){
        des=desDataDispatch(des,blackboard);
    }
    let re = /^([^<>]*)<([^<>]*)>([^<>]*)<\/>(.*)$/;
    let ma = des.match(re);
    let captured = [];
    while (ma !== null) {
        if (ma[1].length > 0) {
            captured.push(
                <span>
                    {ma[1]}
                </span>
            );
        }
        captured.push(
            <InnerDesParser jsxStuff={ma[2]} text={ma[3]}/>
        );
        des = ma[4];
        ma = des.match(re);
    }
    captured.push(
        <span>
            {des}
        </span>
    );
    if(captured.length===0){
        return <span/>
    }
    return captured;
}
DescriptionParser.ropTypes={
    raw: PropTypes.string.isRequired,
};
export default DescriptionParser;