import React from "react";
import {withStyles} from "@material-ui/core";
import PropTypes from "prop-types";

const factionList = ['logo_abyssal', "logo_laterano", "logo_reunion", "logo_ursus", "logo_blacksteel", "logo_leithanien", "logo_rhine", "logo_victoria", "logo_kazimierz", "logo_lungmen", "logo_rhodes", "logo_kjerag", "logo_penguin", "logo_rim",]

const style=theme=>({
    root:{
        height:'180px',
        width:'180px',
        backgroundImage:(props)=>`url('./img/factions/${(factionList.includes(props.faction) ? props.faction : 'logo_rhodes')}.png')`,
        // backgroundImage:(props)=>`url(${require('./img/factions/'+(factionList.includes(props.faction) ? props.faction : 'logo_rhodes')+'.png')})`,
        backgroundSize:'100% 100%',
        backgroundPosition:'center',
        backgroundRepeat:'no-repeat',
        position:'absolute',
        top:'-20px',
        left:'-20px',
        clip:'rect(20px,200px,200px,20px)',
        filter: 'drop-shadow(0px 0px 4px rgb(0, 0, 0))',
    },
});

function main(props) {
    const {classes}=props
    return <div className={classes.root}/>
}

main.propTypes = {
    faction: PropTypes.string.isRequired,
};

export default withStyles(style)(main);