import IconButton from "@material-ui/core/IconButton";
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import React from "react";
import {withStyles} from "@material-ui/core";
import {fade} from "@material-ui/core/styles";
import Slider from "@material-ui/lab/Slider";
import PropTypes from "prop-types";

import png_0 from './img/ui/rank/0.png';
import png_1 from './img/ui/rank/1.png';
import png_2 from './img/ui/rank/2.png';
import png_3 from './img/ui/rank/3.png';
import png_4 from './img/ui/rank/4.png';
import png_5 from './img/ui/rank/5.png';
import png_6 from './img/ui/rank/6.png';
import png_7 from './img/ui/rank/7.png';
import png_8 from './img/ui/rank/8.png';
import png_9 from './img/ui/rank/9.png';
import png_10 from './img/ui/rank/10.png';

const lvIcon = [png_0,png_1,png_2,png_3,png_4,png_5,png_6,png_7,png_8,png_9,png_10]
const styledBy = (property, mapping) => props => mapping[props[property]];

const styles = theme => ({
    root:{
        paddingLeft: '24.5px',
        display:'flex',
    },
    lvbg:{

        backgroundImage:'url('+lvIcon[0]+')',
        backgroundSize:"27%",
        backgroundPosition:'center',
        backgroundRepeat:"repeat",
        boxShadow: 'rgba(7, 7, 7, 0.75) 0px 3px 10px 2px',
        width:"60px",
        height:"60px",
    },
    lvbutton: {
        borderRadius: 0,
        padding:'0px',
        margin:'12px',
        zIndex:100
    },
    sliderWrap:{
        padding: '0px 15px 0px 5px',
        width:"100%",
        margin:'auto',
        display:'flex'
    },
    slider: {
        width: '200px',
        padding: '22px 0px',
        margin: '5px 20px 5px 20px',
    },
});

const levelIconStyles = theme => ({
    root:{
        width:"60px",
        height:"60px",
        backgroundImage:props=> 'url('+lvIcon[props.lv]+')',
        backgroundSize:"100%",
        backgroundPosition:'center',
        backgroundRepeat:"no-repeat",
    }
});

function levelIcon(props) {
    const {classes} = props;
    return <div className={classes.root}/>;
}

levelIcon.propTypes = {
    lv: PropTypes.number.isRequired,
};

const LevelIcon = withStyles(levelIconStyles)(levelIcon);



const SkLevelSlider = withStyles({
    thumb: {
        borderRadius: 0,
        height: 24,
        width: 24,
        backgroundColor: '#595959',
        border: '2px solid #595959',
        '&$focused, &:hover': {
            boxShadow: `0px 0px 0px ${4}px ${fade('#595959', 0.16)}`,
        },
        '&$activated': {
            backgroundColor: '#fff',
            boxShadow: `0px 0px 0px ${4 * 1.5}px ${fade('#595959', 0.16)}`,
        },
        '&$jumped': {
            boxShadow: `0px 0px 0px ${4 * 1.5}px ${fade('#595959', 0.16)}`,
        },
    },
    track: {
        backgroundColor: '#595959',
        height: 8,
    },
    trackAfter: {
        backgroundColor: '#b2b2b2',
    },
    focused: {},
    activated: {},
    jumped: {},
})(Slider);

function main(props) {
    const {classes, level,levelHook}=props;
    return(
        <div className={classes.root}>
            <div className={classes.lvbg}>
                <LevelIcon lv={level+1}/>
            </div>
            <div className={classes.sliderWrap}>
                <IconButton className={classes.lvbutton} onClick={()=>(level>0)?levelHook(level-1):null} disabled={level <=0}>
                    <KeyboardArrowLeft />
                </IconButton>
                <SkLevelSlider
                    className={classes.slider}
                    value={level}
                    min={0}
                    max={9}
                    step={1}
                    onChange={(e, value) => levelHook(value)}
                />
                <IconButton className={classes.lvbutton} onClick={()=>(level<9)?levelHook(level+1):null} disabled={level >= 9}>
                    <KeyboardArrowRight />
                </IconButton>
            </div>
        </div>
    )
}

export default withStyles(styles)(main);