import React from "react";
import itemTable from './excel/item_table'
import withStyles from "@material-ui/core/styles/withStyles";
import PropTypes from "prop-types";

const rarityColor = [
    {color:'rgb(160,160,160)'},
    {color:'rgb(217,229,59)'},
    {color:'rgb(1,178,248)'},
    {color:'rgb(180,148,182)'},
    {color:'rgb(253,201,4)'},
    {color:'rgb(253,201,4)'},
]


//

function parserData() {
    let data = {};
    Object.keys(itemTable.items).forEach(key => data[key] = {
        id:itemTable.items[key].itemId,
        name:itemTable.items[key].name,
        rarity:itemTable.items[key].rarity,
        type:itemTable.items[key].name.includes('')?'CHIP':(itemTable.items[key].name.includes('')?'SKILL':itemTable.items[key].itemType),
    });
    data[32001].type='CHIP';
    return data
}

const itemData=parserData()

function classifier(x, key, category) {
    let y={};
    category.forEach(i=>
        y[i]=x.filter(x=>itemData[x][key]===i));
    return y;
}

function sortByRarity(input) {
    return input.sort((a,b)=> itemData[b].rarity-itemData[a].rarity)
}

const iconStyle = theme=>({
    root:{
        backgroundImage:props=>`url(${require('./img/items/bg/'+String(props.rarity)+'.png')})`,
        display:'flex',
        backgroundSize: 'auto 90%',
        backgroundPosition:'center center',
        backgroundRepeat:'no-repeat',
        height:'50px',
        width:'50px',
        position:'relative',
    },
    icon:{
        backgroundImage:props=>`url('./img/items/${props.id}.png')`,
        backgroundSize: 'contain',
        backgroundPosition:'center center',
        backgroundRepeat:'no-repeat',
        margin:'auto',
        height:'87.5%',
        width:'87.5%',
    },
});

function itemIcon(props) {
    const {classes,id, rarity, ...other}=props;
    return (
        <div className={classes.root} {...other}>
            <div className={classes.icon}/>
        </div>
    )
}
itemIcon.propTypes = {
    id: PropTypes.string.isRequired,
    rarity: PropTypes.number.isRequired,
    count: PropTypes.number,
};
const RawItemIcon = withStyles(iconStyle)(itemIcon);
function ItemIcon(props) {
    const {id,...other}=props;
    return(
        <RawItemIcon
            id={itemTable.items[id].iconId}
            rarity={itemTable.items[id].rarity}
            title={itemTable.items[id].name}
            {...other}
        />
    )
};

function itemGet(id,name) {
    return itemTable.items[id][name];
}

function itemCountStyled(id,count) {
    return (<span style={rarityColor[itemTable.items[id].rarity]}>{itemTable.items[id].name + ' x'+count.toString()}</span>)
}

export {ItemIcon, itemCountStyled, itemGet, classifier, sortByRarity, itemData};

