import React from 'react';
import rangidTable from './excel/range_table';
import PropTypes from "prop-types";


const d_0 = 2;
const h_0 = 16;

function boxPath(x, y, d, h) {
    let sx = x.toString();
    let sy = y.toString();
    let sh = h.toString();
    return (
        <path d={['M', sx, sy, 'h', sh, 'v', sh, 'H', sx, 'Z'].join(' ')}/>
    )
}

function raw(props) {
    const {id, color="#fff", width, height}=props;
    if (id===null||id===undefined){
        let h = height?(width>height?height:width):(width?width:h_0);
        return (
            <svg xmlns="http://www.w3.org/2000/svg" width={width.toString()} height={height.toString()}>
                <path d={['M',(width-h/3)/2,(height-h/3)/2+h/3,'l',h/3,-h/3].map(x=>x.toString()).join(' ')} stroke={color} stroke-width="2px"/>
            </svg>
        );
    }
    let data = rangidTable[id].grids;
    let sr = 0, sc = 0, lr = 0, lc = 0;
    let box = [];
    for (let x of data) {
        sr = sr < x.row ? sr : x.row;
        sc = sc < x.col ? sc : x.col;
        lr = lr > x.row ? lr : x.row;
        lc = lc > x.col ? lc : x.col;
    }

    let width_0 = (h_0 + d_0) * (1 + lc - sc) + d_0;
    let height_0 = (h_0 + d_0) * (1 + lr - sr) + d_0;
    let d = d_0;
    let h_1 = h_0;
    let h_2 = h_0;

    if(width){
        h_1 = (width-d)/(1+lc-sc)-d;
        width_0 = width;
    }
    if(height){
        h_2 = (height-d)/(1+lr-sr)-d;
        height_0=height;
    }

    let h=Math.min(h_0, h_1,h_2);
    let width_1=(h + d_0) * (1+lc-sc)+d_0;
    let height_1=(h + d_0) * (1+lr-sr)+d_0;

    let dx = (width_0-width_1)/2;
    let dy = (height_0-height_1)/2;


    for (let x of data) {
        if (x.row !== 0 || x.col !== 0) {
            box.push(boxPath(dx + (h + d) *(-sc+x.col) + d, dy + (h + d) * (-sr+x.row) + d, d, h));
        }
    }

    return (
        <svg
            viewBox={[0, 0, width_0, height_0]}
            width={width_0}
            height={height_0}
        >
            {
                [
                    <g fill={color} stroke={color} strokeWidth={1}>{boxPath(dx-(h + d) *sc+d, dy -(h + d) * sr + d, d, h)}</g>,
                    <g fill="none" stroke={color} strokeWidth={1}>
                        {box}
                    </g>,
                ]
            }
        </svg>
    )
}

raw.propTypes = {
    id: PropTypes.string.isRequired,
    color:PropTypes.string,
    width:PropTypes.number,
    height:PropTypes.number,
};

export default raw;