import char_table from './excel/character_table'
import {chars_buff} from "./buildingData";
import pinyin from "tiny-pinyin";
import React from "react";

const raw_id_list = Object.keys(char_table);
const fetchId = /[^_]+$/;



function parserData() {
    let data = {};
    raw_id_list.forEach(key => data[key] = {
        rawId: key,
        id: fetchId.exec(key)[0],
        name: char_table[key].name,
        appellation: char_table[key].appellation,
        pinyin:pinyin.convertToPinyin(char_table[key].name),
        py:pinyin.convertToPinyin(char_table[key].name,'-').split('-').map(x=>x.charAt(0)).join(''),
        position: char_table[key].position,
        tagList: char_table[key].tagList,
        profession: char_table[key].profession,
        description: char_table[key].description,
        displayLogo: char_table[key].displayLogo,
        team: char_table[key].team,
        rarity: char_table[key].rarity,
        maxPhase: char_table[key].phases.length - 1,
        phases: char_table[key].phases.map(x => ({
            rangeId: x.rangeId,
            maxLevel: x.maxLevel,
            attributesKeyFrames: x.attributesKeyFrames,
        })),
        favorKeyFrames: char_table[key].favorKeyFrames,
        talents: char_table[key].talents,
        potentialRanks: char_table[key].potentialRanks.map(x => x.description),
        evolveCost: char_table[key].phases.map(x => x.evolveCost),
        skills: char_table[key].skills.map(x=>(x.skillId)),
        masterSkillCost: char_table[key].skills.map(x => x.levelUpCostCond.map(y => y.levelUpCost)),
        allSkillLvlup: char_table[key].allSkillLvlup.map(x => x.lvlUpCost),
        buff: chars_buff[key]?chars_buff[key].buffChar.map(x => x.buffData):null,
    });
    return data;
}
const data = parserData();
// console.log(data);
export {raw_id_list,data};