import Avatar from "@material-ui/core/Avatar";
import professionSVG from "./svg/professionSVG";
import Chip from "@material-ui/core/Chip";
import {withStyles} from "@material-ui/core";
import React from "react";
import PropTypes from "prop-types";
import grey from "@material-ui/core/colors/grey";
import indigo from "@material-ui/core/colors/indigo";
import deepPurple from "@material-ui/core/colors/deepPurple";
import yellow from "@material-ui/core/colors/yellow";
import orange from "@material-ui/core/colors/orange";

const styledBy = (property, mapping) => props => mapping[props[property]];
const rarityColors = [grey, grey, indigo, deepPurple, yellow, orange];

const styles = theme => ({
    avatar: {
        padding: 4,
        background: styledBy('rarity', {
            0: rarityColors[0][500],
            1: rarityColors[1][500],
            2: rarityColors[2][500],
            3: rarityColors[3][500],
            4: rarityColors[4][500],
            5: rarityColors[5][500],
            7: "#000"
        }),
        backgroundImage: styledBy('pro', professionSVG),
        backgroundSize: '75% auto !important',
        backgroundRepeat: 'no-repeat !important',
        backgroundPosition: 'CENTER !important',
        color: '#FFF',
    },
    chip: {
        margin: theme.spacing.unit,
        background: styledBy('rarity', {
            0: rarityColors[0][200],
            1: rarityColors[1][200],
            2: rarityColors[2][200],
            3: rarityColors[3][200],
            4: rarityColors[4][200],
            5: rarityColors[5][200],

        }),
    },
});

function displayCharChip(props) {
    const {classes, pro, name, onClick} = props;
    let proAvatar = <Avatar alt={pro} className={classes.avatar}> {!(pro in professionSVG) && "?"} </Avatar>    ;
    return (
        <div onClick={onClick}>
            <Chip
                avatar={proAvatar}
                label={name}
                className={classes.chip}
            />
        </div>
    )
}

displayCharChip.propTypes = {
    classes: PropTypes.object.isRequired,
    rarity: PropTypes.number.isRequired,
    pro: PropTypes.string.isRequired,
}

export default withStyles(styles)(displayCharChip);