import React, {useState} from 'react';
import PropTypes from 'prop-types';
import {createMuiTheme, withStyles} from '@material-ui/core/styles';
import {ThemeProvider} from '@material-ui/styles';

import CharChip from './characterChip';
import SkillDisplay from './skillRender'
import LevelSlider from './skillLevelSlider'
import CharacterCard from './characterCard'
import CostCount from './calculate';
// import data from "./excel/character_table.json";
import {data} from './chatacterData';

import Typography from "@material-ui/core/Typography";

import {Paper} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Input from "@material-ui/core/Input";
import FormHelperText from "@material-ui/core/FormHelperText";

const char_list = Object.keys(data).reverse();

const professionList = ["ALL", "CASTER", "TANK", "WARRIOR", "MEDIC", "SNIPER", "SPECIAL", "SUPPORT", "PIONEER", "TOKEN"];
const lazyList=["TOKEN",'TRAP'];

const arkTheme = createMuiTheme({
    palette: {
        primary: {
            light: '#434343',
            main: '#1c1c1c',
            dark: '#000000',
            contrastText: '#fff',
        },
        secondary: {
            light: '#434343',
            main: '#1c1c1c',
            dark: '#000000',
            contrastText: '#fff',
        },
    },
});

const styles = theme => ({
    root: {
        display: 'flex',
        justifyContent: 'center',
        flexWrap: 'wrap',
    },
    navigateWrap:{
        margin:'10px 0',
        display: 'flex',
        justifyContent:'space-between',
        width:'100%',
        '&>div':{
            display:'flex',
        },
        '&>div>div':{
            display:'flex',
            border:'1px solid #000',
            borderRadius:'2px',
            padding:'1px 3px',
            margin:'0 5px',
            userSelect:'none',
        },
        '&>div>div:active':{
            color:'#fff',
            background:'#000',
        },

    },
    form: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    formControl: {
        margin: '8px',
    },
    paper: {
        minWidth:`${562+16*2}px`,
        padding: theme.spacing(3, 2),
        // width:"100%",
    },
});


function Chips(props) {
    const {classes} = props;
    const [name, setName] = useState('');
    const [chosenName,setChosenName] = useState('');
    let [dataChosen, setDataChosen] = useState({});
    let [selectedPro, setSelelctedPro] = useState(professionList.map((key) => true));
    let [skLevel, setSkLevel] = useState(0);

    function checkHandle(index, state) {
        let temp = selectedPro.map(k => k);
        if (index === 0) {
            temp.fill(state);
            setSelelctedPro(temp);
            return;
        } else if (!state) {
            temp[0] = state;

        }
        temp[index] = state;
        if (state && temp.indexOf(false, 1) < 0) {
            temp[0] = true;
        }
        setSelelctedPro(temp);
        return;
    }

    function selectData(id) {
        // setDataId(id)
        setName(data[id].name);
        setChosenName(data[id].name + '/'+ data[id].appellation+'/'+data[id].id);
        setDataChosen(data[id]);
    }

    function textHandleChange(event) {
        if (event.target.value.trim().length===0){
            setChosenName('');
        }else if(event.target.value.trim() in data) {
            let temp = event.target.value.trim()
            setChosenName(data[temp].name + '/'+ data[temp].appellation+'/'+data[temp].id);
            setDataChosen(data[temp]);
            setName(data[temp].name);
            return
        }else {
            let re = new RegExp('^' + event.target.value.trim(), 'i');
            let filtered = Object.keys(data).filter(function (key) {
                if (re.test(data[key].id)) {
                    return true
                }
                if (re.test(data[key].appellation)) {
                    return true
                }
                if (re.test(data[key].name)) {
                    return true
                }
            });
            if (filtered.length === 1){
                setChosenName(data[filtered[0]].name+'/'+data[filtered[0]].appellation+'/'+data[filtered[0]].id);
                setDataChosen(data[filtered[0]]);
            }
        }
        setName(event.target.value);
    }

    function NextHandle() {
        let cu = char_list.indexOf(dataChosen.rawId);
        let le = char_list.length;
        if(cu>-1){
            while (1){
                if(cu <le-1 ){
                    if(lazyList.includes(data[char_list[cu+1]].profession)){
                        cu++;
                    }else {
                        selectData(char_list[cu + 1]);
                        return;
                    }
                }else{
                    selectData(char_list[0]);
                    return;
                }
            }
        }
    }
    function PreHandle() {
        let cu = char_list.indexOf(dataChosen.rawId);
        let le = char_list.length;
        if(cu>-1){
            while (1){
                if(cu >0 ){
                    if(lazyList.includes(data[char_list[cu-1]].profession)){
                        cu--;
                    }else {
                        selectData(char_list[cu -1]);
                        return;
                    }
                }else{
                    selectData(char_list[le]);
                    return;
                }
            }
        }
    }

    function randomHandle() {
        let x=0;
        let i=0
        while (i<20) {
            x = Math.round(Math.random() * (char_list.length-1));
            if(!lazyList.includes(data[char_list[x]].profession)) {
                selectData(char_list[x]);
                return;
            }
            i++;
        }
        selectData(char_list[0]);
        return;
    }

    return (
        <ThemeProvider theme={arkTheme}>
            {[
                <div className={classes.form}>
                    <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="component-simple">Name/ID</InputLabel>
                        <Input id="component-simple"  value={name} onChange={textHandleChange}/>
                        <FormHelperText id="component-helper-text">{chosenName}</FormHelperText>
                    </FormControl>
                </div>,
            <div className={classes.navigateWrap}>
                <div>
                    <div onClick={PreHandle}>Previous</div>
                </div>
                <div>
                    <div onClick={NextHandle}>Next</div>
                    <div onClick={randomHandle}>Random</div>
                </div>
            </div>,

            <CharacterCard data={dataChosen}/>,
            <br/>,
            (dataChosen.name &&
                <Paper className={classes.paper}>
                    {dataChosen.skills &&
                    [<LevelSlider level={skLevel} levelHook={setSkLevel}/>,
                        <table style={{borderCollapse: "separate", borderSpacing: '0px 15px', width: '100%'}}>
                            {dataChosen.skills.length > 0 ? dataChosen.skills.map((skillId,index) => (
                                <SkillDisplay skId={skillId} level={skLevel} levelup={dataChosen.masterSkillCost[index]} normalLevelup={dataChosen.allSkillLvlup}/>
                            )) : "No skill."}
                        </table>
                    ]
                    }
                </Paper>
            ),
            (dataChosen.name &&
                <br/>
            ),
                (dataChosen.name &&
                    <Paper className={classes.paper}>
                        <CostCount id={dataChosen.rawId}/>
                    </Paper>
                ),
                (dataChosen.name &&
                    <br/>
                ),
            <Paper className={classes.paper}>
                <Grid
                    container
                    direction="row"
                    justify="flex-start"
                    alignItems="center"
                    spacing={3}
                >
                    <Grid item>
                        <Typography component='h6'>Filter:</Typography>
                    </Grid>
                    <Grid item>
                        <FormGroup row>
                            {professionList.map((key, index) => (
                                <FormControlLabel control={<Checkbox checked={selectedPro[index]}
                                                                     onChange={(e) => checkHandle(index, e.target.checked)}/>}
                                                  label={key}
                                />
                            ))}
                        </FormGroup>
                    </Grid>
                </Grid>
            </Paper>,
            <br/>,
            <Paper className={classes.paper}>
                <div className={classes.root}>
                    {
                        !selectedPro.includes(true) ? (<Typography variant="h5" component="h3">
                                Select at least one profession.
                            </Typography>

                        ) : char_list.map(id => (
                            (selectedPro[0] || selectedPro[professionList.indexOf(data[id].profession)]) &&
                            <CharChip
                                name={data[id].name}
                                pro={data[id].profession}
                                className={classes.chip}
                                rarity={(professionList.includes(data[id].profession) && data[id].profession !== 'TOKEN') ? data[id].rarity : 0}
                                onClick={() => selectData(id)}
                            />
                        ))
                    }
                </div>
            </Paper>
        ]}
        </ThemeProvider>
    );
}

Chips.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Chips);