const potentialIconStyle = theme =>({
    root:{
        backgroundImage:props=> 'url(./img/ui/potential/'+(props.type+1).toString()+'.png)',
        backgroundSize:"100%",
        backgroundRepeat:'no-repeat',
        backgroundPosition:'center',
        width:'20px',
        height:'20px',
        marginRight:'2px',
    },
});

function potentialIcon(props) {
    const {classes}=props;
    return <div className={classes.root}/>
}

potentialIcon.propTypes = {
    type: PropTypes.number.isRequired,
};

const PotentialIcon=withStyles(potentialIconStyle)(potentialIcon);

const attrIconStyle = theme =>({
    root:{
        backgroundImage:props=> 'url('+attrDict[props.type].icon+')',
        backgroundSize:"100%",
        backgroundRepeat:'no-repeat',
        backgroundPosition:'center',
        width:'20px',
        height:'20px',
    },
});

function arrtIcon(props) {
    const {classes}=props;
    return <div className={classes.root}/>
}

arrtIcon.propTypes = {
    type: PropTypes.string.isRequired,
};

const AttrIcon=withStyles(attrIconStyle)(arrtIcon);

const buffIconStyle = theme =>({
    root:{
        backgroundImage:props=> `url("./img/ui/infrastructure/${props.type}.png")`,
        backgroundSize:"100%",
        backgroundRepeat:'no-repeat',
        backgroundPosition:'center',
        width:'16px',
        height:'16px',
    },
});

function buffIcon(props) {
    const {classes}=props;
    return <div className={classes.root}/>
}

buffIcon.propTypes = {
    type: PropTypes.string.isRequired,
};

const BuffIcon=withStyles(buffIconStyle)(buffIcon);

const eliteIconStyle = theme =>({
    root_0:{
        backgroundImage:elite_svg[0],
        backgroundSize:"100%",
        backgroundRepeat:'no-repeat',
        backgroundPosition:'center',
        width:'60px',
        height:'60px',
    },
    root_1:{
        backgroundImage:elite_svg[1],
        backgroundSize:"100%",
        backgroundRepeat:'no-repeat',
        backgroundPosition:'center',
        width:'60px',
        height:'60px',
    },
    root_2:{
        backgroundImage:elite_svg[2],
        backgroundSize:"100%",
        backgroundRepeat:'no-repeat',
        backgroundPosition:'center',
        width:'60px',
        height:'60px',
    },
});

function eliteIcon(props) {
    const {classes,phase, ...other}=props;
    switch (phase) {
        case 0: return <div {...other} className={classes.root_0}/>;
        case 1 : return <div {...other} className={classes.root_1}/>;
        case 2 : return <div {...other} className={classes.root_2}/>;
    }
}

eliteIcon.propTypes = {
    phase: PropTypes.number.isRequired,
};

const EliteIcon=withStyles(eliteIconStyle)(eliteIcon);

function Main(props) {
    const {classes, data} = props;
    let [phase, setPhase] = useState(0);
    let [level, setLevel] = useState(1);
    let [levelSliderStart, setLevelSlider] = useState(1);
    let [favor, setFavor] = useState(100);
    let [frontLayer,setFrontLayer] = useState('');

    if (data === undefined || !('name' in data)) {
        return (
            <div>
                <Paper style={{padding:'10px', textAlign:'center'}} className={classes.paper}>
                    <Typography variant="h5" component="h3">
                        Select a character.
                    </Typography>
                </Paper>
            </div>
        );
    };

    const raw_id = data.rawId;
    const maxPhase = data.maxPhase;

    if (phase > maxPhase) {
        setPhase(maxPhase);
        return
    }
    if (level > data.phases[phase].maxLevel) {
        setLevel(data.phases[phase].maxLevel);
    }

    function closeMask(){
        setFrontLayer('');
    }

    function allMax() {
        setPhase(2);
        setLevel(data.phases[maxPhase].maxLevel);
        setLevelSlider(1);
        setLevelSlider(data.phases[maxPhase].maxLevel);
    }

    function buffName(id) {
        const buffData = buff_table[id];
        return(
            <div className={classes.buffName} style={{color:buffData.textColor,backgroundColor:buffData.buffColor}}>
                <BuffIcon type={buffData.buffIcon}/>
                {buffData.buffName}
            </div>
        )
    }

    function showAttr(data = null, phase = 0, lv = 1, f = 0) {
        if (data === null || data.phases == null) {
            return;
        }
        let effFavor = (f <= 100 ? f : 100);
        const toShow = Object.keys(attrDict);
        const digit = ['baseAttackTime']
        const maxLv = data.phases[phase].maxLevel;
        let favorPhase = toShow.map(key => (data.favorKeyFrames != null ? data.favorKeyFrames[1].data[key] / 50 * favorFrames[effFavor] : 0));
        let attrFrame = toShow.map(key => (
                {
                    name: key,
                    begin: data.phases[phase].attributesKeyFrames[0].data[key],
                    interval: maxLv === 1 ? 0 : (data.phases[phase].attributesKeyFrames[1].data[key] - data.phases[phase].attributesKeyFrames[0].data[key]) / (maxLv - 1),
                }
            )
        );
        const calc = (f, l) => digit.includes(f.name) ? (f.begin + f.interval * (l - 1)).toFixed(2) : Math.round(f.begin + f.interval * (l - 1)).toString();
        return (
            <div className={classes.attrWrap}>
                {[0, 4].map(sliceIndex => (
                        <div>
                            {
                                toShow.slice(sliceIndex, sliceIndex + 4).map((key, index) => (
                                        <div>
                                            <div className={classes.attrNameWrap}>
                                                <AttrIcon type={key}/>
                                                <span>
                                                     {attrDict[key].name}
                                                </span>
                                            </div>
                                            <div className={classes.attrNum}>
                                                <span>{calc(attrFrame[index+sliceIndex], lv)}</span>
                                                {favorPhase[index+sliceIndex] > 0 &&
                                                <span className={classes.favorColor}>
                                                    {' +' + (digit.includes(key) ? favorPhase[index+sliceIndex].toFixed(2) : Math.round(favorPhase[index+sliceIndex]).toString())}
                                                </span>
                                                }
                                            </div>
                                        </div>
                                    )
                                )
                            }
                        </div>
                    )
                )
                }
            </div>
        );

    };

    return (
        <div className={classes.root}>
            <FactionLogo faction={data.displayLogo}/>
            <div className={classes.zoomIconWrap}>
                <ZoomIcon onClick={()=>setFrontLayer('artLayer')}/>
            </div>

            {(frontLayer!=='')&&
                <div className={classes.mask} onClick={closeMask}>
                    {(frontLayer==='talent'&&
                        <div className={classes.dialogWrap} onClick={(e=>e.stopPropagation())}>
                            
                            {(data.talents&&data.talents.length>0 &&(
                                data.talents.map((x,index)=>(
                                    (x.candidates ?
                                            x.candidates.map( y=>(
                                                    <div className={classes.talentDetail}>
                                                        <div>
                                                            <div className={classes.talentName}>
                                                                {String(y.name)}
                                                            </div>
                                                            <div>{'Lv.'+y.unlockCondition.level.toString()}</div>
                                                            {y.unlockCondition.phase>0&&<EliteIcon style={{height:'20px',width:'20px'}} phase={y.unlockCondition.phase}/>}
                                                            {y.requiredPotentialRank>0&&<PotentialIcon type={y.requiredPotentialRank}/>}
                                                        </div>
                                                        <div>
                                                            <p><DescriptionParser raw={y.description} blackboard={y.blackboard}/></p>
                                                        </div>
                                                    </div>
                                                )
                                            )
                                            :null
                                    )
                                ))
                            ))||(
                                <div style={{margin:'5px'}}>None</div>
                            )}
                        </div>
                    )||(frontLayer==='evolveCost'&&
                        <div className={classes.dialogWrap} onClick={(e=>e.stopPropagation())}>
                            
                            {data.evolveCost.map((costList, index)=>(
                                costList && costList.length>0 ?
                                        <div className={classes.eliteMaterial}>
                                            <EliteIcon style={{width:'50px',height:'50px'}} phase={index}/>
                                            {
                                                costList.map((item_data) => (
                                                    <div>
                                                        <ItemIcon id={item_data.id}/>
                                                        {'x'+String(item_data.count)}
                                                    </div>
                                                    )
                                                )
                                            }
                                            </div>:null
                                ))

                            }
                        </div>
                    )||(frontLayer==='buffDetail'&&
                        <div className={classes.dialogWrap} onClick={(e=>e.stopPropagation())}>
                            
                            {(data.buff&&(
                                data.buff.map((buffData,index)=>(
                                    (buffData.length>0?
                                            buffData.map( x=>(
                                                    <div className={classes.buffDetail}>
                                                        <div>
                                                            {buffName(x.buffId)}
                                                            <div>{'Lv.'+x.cond.level.toString()}</div>
                                                            {x.cond.phase>0&&<EliteIcon style={{height:'20px',width:'20px'}} phase={x.cond.phase}/>}
                                                        </div>
                                                        <div>
                                                            <p><DescriptionParser raw={buff_table[x.buffId].description}/></p>
                                                        </div>
                                                    </div>
                                                )
                                            )
                                            :null
                                    )
                                ))
                            ))||(
                                <div style={{margin:'5px'}}>None</div>
                            )}
                        </div>
                    )}

                </div>
            }
            {(frontLayer!=='artLayer' &&
                <div className={classes.artLayer}
                     style={{
                         backgroundImage: "url('./img/characters/" +
                             raw_id + "_" +
                             String(phase > 1 ? 2 : 1) + ".png')",
                         backgroundSize: phase > 1 ? 'auto 115%' : 'auto 90%',
                         backgroundPosition: 'center center',
                         backgroundRepeat: 'no-repeat',
                     }}
                />)||
            (<div className={classes.artLayer}
                     style={{
                         backgroundColor:'rgba(0,0,0,0.8)',
                         backgroundImage: "url('./img/characters/" +
                             raw_id + "_" +
                             String(phase > 1 ? 2 : 1) + ".png')",
                         backgroundSize: phase > 1 ? 'auto 100%' : 'auto 100%',
                         backgroundPosition: 'center center',
                         backgroundRepeat: 'no-repeat',
                         zIndex:1000,
                     }}
                     onClick={closeMask}
                />)
            }

            <div className={classes.leftWrap}>
                <div className={classes.attrHead}>
                    <FastForward/>
                </div>
                {showAttr(data, phase, level, favor)}
                <div className={classes.favorWrap}>
                    <div className={classes.favorHeadWrap}>
                        <div><FastForward/></div>
                        <div>{`${favor}%`}</div>
                    </div>
                    <FavorSlider value={favor} handle={setFavor}/>
                </div>
                <div className={classes.starWrap}>
                    {Array(data.rarity+1).fill(1).map(()=><div/>)}
                </div>
                <div className={classes.charNameWrap}>
                    <div>{data.appellation}</div>
                    <div>{data.name}</div>
                </div>
                <div className={classes.leftBottomWrap}>
                    <ProfessionIcon type={data.profession}/>
                    <div className={classes.rangeWrap}>
                        <RangeId id={data.phases[phase].rangeId} color={'#fff'} width={80} height={40}/>
                        <span></span>
                    </div>
                    <div className={classes.tagWrap}>
                        <div>{data.position}</div>
                        <div>
                            <div className={classes.tag}>{data.tagList?data.tagList.join(' '):''}</div>
                        </div>
                    </div>
                </div>
            </div>

            <div className={classes.RightWrap}>
                <div className={classes.RightTopWrap}>
                    <div className={classes.levelIconWrap}>
                        <CircularProgress className={classes.levelProgress} size={55} thickness={levelProgressWidth*20.5/(27.5-levelProgressWidth)} variant="static" value={level/data.phases[phase].maxLevel*100} />
                        <div className={classes.lvTag}/>
                        <div>{level}</div>
                    </div>
                    <div className={classes.levelSliderWrap}>
                        <Nouislider
                            id={'levelslider'}
                            start={levelSliderStart}
                            connect={[true, false]}
                            // tooltips={[ wNumb({decimals: 0})]}
                            behaviour="tap"
                            step={1}
                            range={{
                                min: [1],
                                max: [data.phases[phase].maxLevel]
                            }}
                            onSlide={(render, h, value) =>setLevel(Math.round(value[0]))}
                            onChange={(a,b,c)=>setLevelSlider(c)}
                        />
                    </div>
                </div>
                <div className={classes.eliteWrap}>
                    <div>
                        
                        <div className={classes.eliteIconWrap}><EliteIcon phase={phase}/></div>
                        <div className={classes.elitePlus} onClick={()=>setPhase(phase>=maxPhase?0:(phase+1))}><AddIcon/></div>
                        <div className={classes.moreIconWrap} style={{left:'25px'}} onClick={()=>setFrontLayer('evolveCost')}><MoreIcon/></div>
                    </div>
                    <div className={classes.allMax} onClick={()=>allMax()}><div>All Max</div></div>
                </div>
                <div className={classes.potentialWrap}>
                    
                    <div>
                    {(data.potentialRanks&&data.potentialRanks.length>0 &&(
                        data.potentialRanks.map((x,index)=>(
                            <div><PotentialIcon type={index}/>{x}</div>
                        ))
                    ))||(
                        <div>None</div>
                    )}
                    </div>
                </div>
                <div className={classes.descriptionWrap}>
                    
                    <div><p><DescriptionParser raw={data.description}/></p></div>
                </div>
                <div className={classes.talentWrap}>
                    
                    <div>
                    {(data.talents&&data.talents.length>0 &&(
                        data.talents.map((x,index)=>(
                            (x.candidates ?<div className={classes.talentName}>{x.candidates[0].name}</div>:null
                            )
                        ))
                    ))||(
                        <div style={{margin:'5px'}}>None</div>
                    )}
                    </div>
                    <div className={classes.moreIconWrap} onClick={()=>setFrontLayer('talent')}><MoreIcon/></div>
                </div>
                <div className={classes.talentWrap}>
                    
                    <div>
                        {(data.buff&&(
                            data.buff.map((buffData)=>(
                                (buffData.length>0? buffName(buffData[0].buffId) :null
                                )
                            ))
                        ))||(
                            <div style={{margin:'5px'}}>None</div>
                        )}
                    </div>
                    <div className={classes.moreIconWrap} onClick={()=>setFrontLayer('buffDetail')}><MoreIcon/></div>
                </div>
            </div>
        </div>
    );
}

Main.propTypes = {
    classes: PropTypes.object.isRequired,
    data: PropTypes.object.isRequired,
};

const CharacterCard = withStyles(styles)(Main);

CharacterCard.propTypes ={
    data: PropTypes.object.isRequired,
};

export default CharacterCard;