import React from "react";
import Nouislider from "nouislider-react";
// import wNumb from 'wnumb'
import "nouislider/distribute/nouislider.css";
import "./css/favorSlider.css";

class Slider extends React.Component {
    constructor(x) {
        super(x);
        this.props=x;
        // this.state = {
        //     textValue: null,
        //     percent: null
        // };
    }

    // onSlide = (render, handle, value, un, percent) => {
    //     this.setState({
    //         textValue: value[0].toFixed(2),
    //         percent: percent[0].toFixed(2)
    //     });
    // };

    render() {
        // const { textValue, percent } = this.state;
        return (
            <div>
                <Nouislider
                    id={'favorslider'}
                    start={[this.props.start?this.props.start:100]}
                    connect={[true, false]}
                    // tooltips={[ wNumb({decimals: 0})]}
                    behaviour="tap"
                    range={{
                        min: [0],
                        max: [100]
                    }}
                    onSlide={(render, handle, value, un, percent) =>this.props.handle(value[0].toFixed(0))}
                />
            </div>
        );
    }
}

export default Slider;