import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Slide from '@material-ui/core/Slide';
import ListItem from '@material-ui/core/ListItem';
import PortraitIcon from '@material-ui/icons/Portrait';
import ViewComfyIcon from '@material-ui/icons/ViewComfy';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import App1 from './characterView'
import App2 from './test'

import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';


const drawerWidth = 240;
const appName = ['',''];
const webName = 'Ark Tools';
const menuList = ['',''];
const iconList = [PortraitIcon,ViewComfyIcon];

const arkTheme = createMuiTheme({
    palette: {
        primary: {
            light: '#434343',
            main: '#1c1c1c',
            dark: '#000000',
            contrastText: '#fff',
        },
        secondary: {
            light: '#434343',
            main: '#1c1c1c',
            dark: '#000000',
            contrastText: '#fff',
        },
    },
});

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        maxWidth:'900px',
        margin:'auto',
    },
    logo: {
        height: '32px',
    },
    appBar: {
        // minWidth:"535px",
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerHeader: {
        display: 'flex',
        alignItems: 'center',
        padding: '0 8px',
        ...theme.mixins.toolbar,
        justifyContent: 'center',
    },
    content: {
        minWidth:`${562+16*2+24*2}px`,
        flexGrow: 1,
        padding: theme.spacing(3),
    },
}));



function HideOnScroll(props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({ target: window ? window() : undefined });

    return (
        <Slide appear={false} direction="down" in={!trigger}>
            {children}
        </Slide>
    );
}

HideOnScroll.propTypes = {
    children: PropTypes.node.isRequired,
    // Injected by the documentation to work in an iframe.
    // You won't need it on your project.
    window: PropTypes.func,
};

function PersistentDrawerLeft() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [app, setApp] = React.useState(1);

    const toggleDrawer = (open) => event => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setOpen(open);
    };

    return (
        <ThemeProvider theme={arkTheme}>
        <div className={classes.root}>
            <CssBaseline />
            <HideOnScroll>
                <AppBar
                position="fixed"
                className={classes.appBar}
                // className={clsx(classes.appBar, {
                //     [classes.appBarShift]: open,
                // })}
                achor={"top"}
            >
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="Open drawer"
                        onClick={toggleDrawer(true)}
                        edge="start"
                        className={clsx(classes.menuButton, open && classes.hide)}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" noWrap>
                        { appName[app] }
                    </Typography>
                </Toolbar>
            </AppBar>
            </HideOnScroll>
            <Drawer
                className={classes.drawer}
                anchor="left"
                open={open}
                onClose={toggleDrawer(false)}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>

                    <Typography variant="h5"  >
                        {webName.toUpperCase()}
                    </Typography>
                    {/*<IconButton onClick={handleDrawerClose}>*/}
                    {/*    {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}*/}
                    {/*</IconButton>*/}
                </div>
                <Divider />
                <div
                    onClick={toggleDrawer(false)}
                    onKeyDown={toggleDrawer(false)}
                >
                <List>
                    {menuList.map((text, index) => (
                        <ListItem button key={text} onClick={()=>setApp(index)}>
                            <ListItemIcon>{index===0?<PortraitIcon/>:(index===1?<ViewComfyIcon/>:null)}</ListItemIcon>
                            <ListItemText primary={text} />
                        </ListItem>
                    ))}
                </List>
                </div>
            </Drawer>
            <main className={classes.content}>
                <div className={classes.drawerHeader} />
                {(app===0&&
                    <App1/>)||
                (app===1&&
                    <App2/>)}
            </main>
        </div>
        </ThemeProvider>
    );
}

export default PersistentDrawerLeft;