import building_data from './excel/building_data';

const chars_buff = building_data.chars;
const buff_table  = building_data.buffs;

function parserEvolveFormula(){
    const evolveFormula = Object.keys(building_data.workshopFormulas).filter(x=>building_data.workshopFormulas[x].formulaType === 'F_EVOLVE');
    let data={};
    evolveFormula.forEach(x=>data[building_data.workshopFormulas[x].itemId]={
        goldCost:building_data.workshopFormulas[x].goldCost,
        costs:building_data.workshopFormulas[x].costs,
    })
    return data
}
const evolveFormula = parserEvolveFormula();
// console.log(evolveFormula);
export {chars_buff, buff_table, evolveFormula};